import React, {Component} from 'react';
import SeoTitle from './Title/SeoTitle';
import { translate } from "react-translate";


class FormJobs extends Component {
    constructor() {
        super()
        this.state = {
            name: '',
            email: '',
            subject: '',
            message: ''
        }

        this.handleChange = this.handleChange.bind(this)

    }


    
    handleChange = e => {
        this.setState({[e.target.name]: e.target.value})
    }

    render(){
        let { title, btn } = this.props;
        // const {name, email, subject, message, emailStatus} = this.state;
        return(
            <section className="contact_info_area sec_pad bg_color" id="contact">
                <div className="container">
                    <div className="row">
                        <div className="contact_form col text-center">
                            <SeoTitle Title={title} />
                            <form onSubmit={this.submitForm} className="contact_form_box" method="post" id="contactForm">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="form-group text_box">
                                            <input type="text" id="name" name="name" placeholder="Name" onChange={this.handleChange}/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box">
                                            <input type="text" name="email" id="email" placeholder="Email" onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group text_box">
                                            <input type="text" id="country" name="country" placeholder="Country" onChange={this.handleChange}/>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group text_box">
                                            <input type="text" id="city" name="city" placeholder="City" onChange={this.handleChange}/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box">
                                            <input type="text" name="phone" id="phone" placeholder="Number Phone" onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box">
                                            <input type="text" name="position" id="position" placeholder="Position" onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box">
                                            <input type="file" id="myfile" name="myfile" accept="application/pdf"/>
                                        </div>
                                    </div>
                                </div>
                                <button type="submit" className="btn_six slider_btn">{btn}</button>
                            </form>
                            {/* {emailStatus ? emailStatus : null} */}
                            <div id="success">Your message succesfully sent!</div>
                            <div id="error">Opps! There is something wrong. Please try again</div>
                        </div>
                    </div>
                    
                </div>
            </section>
        )
    }
}


export default translate('contact')(FormJobs);