import React, { Component} from "react";
import ServTitle from '../components/Home/ServTitle';
import JobsTabAreas from '../components/JobsTabAreas';
import JobsTabItems from '../components/JobsTabitems';
import { translate } from "react-translate";

const TabJobsEn = require('../assets/translations/en.json');
const TabJobsEs = require('../assets/translations/es.json');
let lang = null;

class JobsTab extends Component {

  constructor(props) {
    super(props);
  }

  render() {

    let { t , language} = this.props;
   
    if (language === 'en') {
      lang = TabJobsEn
    } else {
      lang = TabJobsEs
    }

    return (
      <section className="jobs_area sec_pad" >
        <div className="container">
             <ServTitle Title={t(this.props.title)} TitleP={t(this.props.titleD)} />

          <div className="jobs_content">
            <ul className="nav nav-tabs jobs_tab" role="tablist">

              {             
                lang.jobs_items.job_area.map((item, i) => {
                return <li className="nav-item fadeInUp" data-wow-delay="0.4s" key={i}> 
                            
                            <JobsTabAreas                    
                              active= {item.active}
                              idArea =  {item.idArea}
                              hrefArea = {item.hrefArea}
                              ariactr = {item.ariactr}
                              nameArea = {item.nameArea}
                            />                                            
                      </li>
                })
              
              }

            </ul>
            <div className="tab-content jobs_tab_content">
              {
                lang.jobs_items.jobtabitem.map((item, i) => {
                  return (
                    <div className="tab-pane fade show active"  id={item.hrefArea} role="tabpanel" aria-labelledby={item.idArea} key={i}>
                      <JobsTabItems  
                        nameJob = {item.nameJob}
                        descripJobs={item.descripJobs}
                        requirJobs={item.requirJobs}
                        sueldo = {item.sueldo}
                        time = {item.time}
                        typecontrato = {item.typecontrato}
                        city = {item.city}
                      />  
                                     
                    </div>
                  )
                })
              
              }             
            </div>
          </div>
        </div>
      </section>
    );
  }
}
export default translate('jobs_items')(JobsTab);
