import React, {Component} from 'react';
import { translate } from "react-translate";



class InfoWidget extends Component {
    render(){
        let { t } = this.props;
        let FooterData = this.props.FooterData;
        var {ftitle} = this.props;
        return(
            <div className="col-lg-4 col-md-6">
            <div className="f_widget company_widget wow fadeInLeft" data-wow-delay="0.2s">
                <a href="/" className="f-logo"><img src={require("../../img/logo-blue.png").default} alt=""/></a>
                    <div className="widget-wrap">
                        <p className="f_400 f_p f_size_15 mb-0 l_height34"><span>{t(this.props.Email)}:</span> <a href="mailto:info@dreamcodesoft.com" className="f_400">info@dreamcodesoft.com</a></p>
                        <p className="f_400 f_p f_size_15 mb-0 l_height34"><span>{t(this.props.Phone)}:</span> <a href="tel:573155790099" className="f_400">+57 315 579 00 99</a></p>
                        <p className="f_400 f_p f_size_15 mb-0 l_height34"><span>{t(this.props.Address)}:</span><a href="https://goo.gl/maps/Ync42KoKat2ckRW86" className="f_400"> Carrera 100 #16-321, <br></br>{t(this.props.AddressItem)}</a></p>
                    </div>
            </div>
        </div>
        )
    }
}

export default translate('info_widget')(InfoWidget);