import React, {Component} from 'react';
import ListClientsitem from './ListClientsitem';
import { translate } from "react-translate";

class ListClients extends Component{

    render(){
    
            let { t } = this.props;

        return(
            <section className="erp_features_area_two sec_pad">
                <div className="container">
                    <ListClientsitem 
                        nameClient='Apple' 
                        rowClass="flex-row-reverse"
                        imgLogo="apple.png" 
                        dClient={t(this.props.dClient_Apple)}
                        />
                    <ListClientsitem 
                        nameClient='Target' 
                        rowClass=""  
                        imgLogo="logo-target.png" 
                        dClient={t(this.props.dClient_Target)}
                        />
                    <ListClientsitem 
                        nameClient='Miraclon' 
                        rowClass="flex-row-reverse"  
                        imgLogo="logo-miraclon.png"
                        dClient={t(this.props.dClient_Miraclon)}
                        />
                    <ListClientsitem 
                        nameClient='LogixBoard' 
                        rowClass=""  
                        imgLogo="logo-logixboard.png"
                        dClient={t(this.props.dClient_LogixBoard)}
                        />
                    <ListClientsitem 
                        nameClient='Banco Falabella' 
                        rowClass="flex-row-reverse"  
                        imgLogo="logo-falabella.png"
                        dClient={t(this.props.dClient_Falabella)}
                        />
                    <ListClientsitem 
                        nameClient='Seguros Bolivar' 
                        rowClass=""  
                        imgLogo="logo-bolivar.png"
                        dClient={t(this.props.dClient_Bolivar)}
                        />
                    <ListClientsitem 
                        nameClient='Global Network Solutions' 
                        rowClass="flex-row-reverse"  
                        imgLogo="logo-global.png"
                        dClient={t(this.props.dClient_GlobalNet)}
                        />
                </div>
            </section>
        )
    }    
}

export default translate('ClientsList')(ListClients);