import React from 'react';
import SecBanner from '../components/Banner/SecBanner';
import ListClients from '../components/Clients/ListClients';
import MetaDecorator from '../components/MetaDecorator';


const Clients = () => {
    return(
        <div className="body_wrapper clients menu_white" >
            <MetaDecorator 
                    title="Clients"
                    description="DreamCode is a professional software development company dedicated to modernizing business objectives with leading-edge technology."
            />
            <SecBanner
                    title_banner="title_banner_Clients" 
                    descri_banner="descri_banner_Clients"
                    idBanner="bannerClients"/>
            <ListClients
                dClient_Apple = "dClient_Apple"
                dClient_Target = "dClient_Target"
                dClient_Miraclon = "dClient_Miraclon"
                dClient_LogixBoard = "dClient_LogixBoard"
                dClient_Falabella = "dClient_Falabella"
                dClient_Bolivar = "dClient_Bolivar"
                dClient_GlobalNet = "dClient_GlobalNet"
            />
        </div>
    )
}
export default Clients;
