import React, {Component} from 'react';
import Reveal from 'react-reveal/Reveal';
import { HashLink } from 'react-router-hash-link';
import { NavLink} from 'react-router-dom';


const widgetEn = require('../../assets/translations/en.json');
const widgetEs = require('../../assets/translations/es.json');
let widget = null;

class AboutWidget extends Component {

    constructor(props) {
        super(props);
      }

    render(){

        let {ftitle , language} = this.props;

        if (language === 'en') {
            widget = widgetEn
          } else {
            widget = widgetEs
          }

        return(
            <Reveal effect="fadeInUp" duration={1200}>
                <div className="col-lg-4 col-md-6">
                    <div className="f_widget about-widget pl_70 wow fadeInLeft" data-wow-delay="0.4s">
                        <h3 className="f-title f_600 t_color f_size_18 mb_40">{ftitle}</h3>
                        <ul className="list-unstyled f_list">
                            {
                                widget.aboutWidget.map((item, i) => {
                                    return(
                                        <li key={i}>

                                            <a href={item.url}>{item.text}</a>
                                            {/* <HashLink activeClass="active" to={item.url} spy={true} smooth={true} offset={0} duration={500}>{item.text}</HashLink> */}
                                        </li>
                                    )
                                })
                            }
                        </ul>
                    </div>
                </div>
            </Reveal>
        )
    }
}

export default AboutWidget;