import React, { Component} from "react";
import SecBanner from '../components/Banner/SecBanner';
import JobsTab from '../components/JobsTab';
import MetaDecorator from '../components/MetaDecorator';


class Jobs extends Component {

    constructor(props) {
        super(props);
        
      }

    render() {

        let {language} = this.props;

        return (

        <div className="body_wrapper menu_white">           
            <MetaDecorator 
                title="Jobs"
                description="DreamCode is a professional software development company dedicated to modernizing business objectives with leading-edge technology."
            />
            <SecBanner 
                    title_banner="title_banner_Jobs" 
                    descri_banner="descri_banner_Jobs"/>
            <JobsTab 
                title="title" 
                titleD="titleD"
                job_area="job_area"
                language = {this.props.language}
                
            />

        </div>
    );
}
}
export default Jobs;