import './App.css';
import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { TranslatorProvider } from "react-translate";
import ScrollToTopRoute from "./ScrollToTopRoute";



/* ---- Pages ----*/
import Home from "./Pages/Home";
import DatabaseService from "./Pages/Database-service";
import TechnologyConsulting from "./Pages/Technology-consulting";
import SoftwareDevelopment from "./Pages/Software-development";
import WebAppServices from "./Pages/Web-app-services";
import NotFound from "./Pages/404";
import About from "./Pages/About"
import PrivacyPolicy from "./Pages/Privacy"
import Devops from "./Pages/Devops";
import Jobs from "./Pages/Jobs";
import Scrum from "./Pages/Scrum";
import Clients from "./Pages/Clients";

/* ---- Menu y Footer ----*/
import OnepageMenu from './components/OnepageMenu';
import Footer from "./components/Footer/Footer";
import FooterData from './components/Footer/FooterData';

class App extends Component{
 	
    constructor(props) {
        super(props);
        this.state = {language: 'en'}; 
        this.languageChange = this.languageChange.bind(this);   }
      
    languageChange(event) {
        this.setState({language: event.target.value});
    }

   _getLayout(lang){
    
	   return (
           
	   <TranslatorProvider translations={require('./assets/translations/'+lang+'.json')}>	
            <Router>   
                <OnepageMenu 
                    menu_txt1= "menu_txt1" menu_txt2= "menu_txt2" menu_txt3= "menu_txt3" 
                    menu_txt4= "menu_txt4" menu_txt5= "menu_txt5" menu_txt6= "menu_txt6" 
                    menu_logo= "menu_logo" menu_txt7= "menu_txt7" menu_txt8= "menu_txt8" 
                    value={this.state.value} 
                    onChangeLanguage={this.languageChange} 
                    /> 
                <Switch>                       
                    <ScrollToTopRoute path="/database-service" component={DatabaseService} />
                    <ScrollToTopRoute path="/clients" component={Clients} />
                    <ScrollToTopRoute path="/technology-consulting" component={TechnologyConsulting} />
                    <ScrollToTopRoute path="/software-development" component={SoftwareDevelopment} />
                    <ScrollToTopRoute path="/web-App-Services" component={WebAppServices} />      
                    <ScrollToTopRoute path="/about" component={About} />     
                    <ScrollToTopRoute path="/devops" component={Devops} />
                    <ScrollToTopRoute path="/jobs" component={() => <Jobs language={this.state.language} />} /> 
                    <ScrollToTopRoute path="/scrum" component={Scrum} />
                    <ScrollToTopRoute 
                        exact={true} path={"/"} 
                        component={() => <Home language={this.state.language} />} 
                    />  
                    <ScrollToTopRoute path="/privacy-policy" 
                        component={() => <PrivacyPolicy language={this.state.language} titlePrivacy="titlePrivacy"/>}
                    />
                    <ScrollToTopRoute component={NotFound} />
               
                </Switch>
                <Footer 
                    value={this.state.value}
                    onChangeLanguage={this.languageChange}

                    FooterData={FooterData}
                    language={this.state.language}
                    privacy_policy="privacy_policy" 
                    aboutUs="aboutUs" 
                    solutions="solutions" />
            </Router>
	   </TranslatorProvider>)
   }

   render(){
	   return(this._getLayout(this.state.language));
            };
};

export default App;