import React , {Component} from 'react';
import AboutWidget from '../FooterWidget/AboutWidget';
import SolutionWidget from '../FooterWidget/SolutionWidget';
import InfoWidget from '../FooterWidget/InfoWidget';
import { HashLink } from 'react-router-hash-link';
import { NavLink} from 'react-router-dom';
import {Link } from 'react-scroll';


import { translate } from "react-translate";


/*const FooterDataEN = require('../assets/translations/en.json');
const FooterDataES = require('../assets/translations/es.json');
let FooterData = null;*/

class Footer extends Component {

    state = {language: 'en'};

    constructor(props) {
        super(props);
        
      }
    

    render(){


        let { onChangeLanguage, value, t} = this.props;
        let fecha = new Date();
        let ano = fecha.getFullYear();

        let FooterData = this.props.FooterData;
    
        return(
            <footer className="footer_area footer_area_four f_bg">
                <div className="footer_top">
                    <div className="container">
                        <div className="row">                                      
                            <InfoWidget 
                               	Email = "info_widgetEmail"
                                Phone = "info_widgetPhone"
                                Address = "info_widgetAddress"
                                AddressItem = "AddressItem" 
                            />

                            <AboutWidget 
                                ftitle={t(this.props.aboutUs)}
                                language = {this.props.language}/>
                                
                            <SolutionWidget 
                                ftitle= {t(this.props.solutions)}
                                language = {this.props.language}/>
                        </div>
                    </div>
                </div>



            <div className="footer_bottom">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-4 col-md-5 col-sm-6">
                            <p className="mb-0 f_400">Copyright © {ano} DreamCode Inc <strong>  All rights reserved.</strong> </p>
                        </div>
                        <div className="col-lg-4 col-md-3 col-sm-6">
                            <div className="f_social_icon_two text-center">                                 
                                <a href="https://www.facebook.com/dreamcode.software/" key="1"><i className="ti-facebook"></i></a>                            
                                <a href="https://www.linkedin.com/company/dreamcode-software" key="3"><i className="ti-linkedin"></i></a>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-4 col-sm-12">
                            <ul className="list-unstyled f_menu text-right">
                                {/* <li><a href=".#">Legal</a></li> */}
                                <li>
                                    <a href="/privacy-policy">{t(this.props.privacy_policy)}</a>
                                    {/* <NavLink activeClass="active" to="/Privacy-policy" spy={true} smooth={true} offset={0} duration={500}><strong></strong></NavLink> */}
                                </li>
                                
                                <li> 
                                <select className="select-lang" defalutValue="" value={value} onChange={(value)=>{onChangeLanguage(value)}} >
                                    <option value="es" >Spanish</option>
                                    <option selected={'selected'} value="en" >English</option>
                                </select>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
    </footer>
        )
    }
}

export default translate('footer')(Footer);