import React, {Component} from 'react';
import { translate } from "react-translate";

const PolicyEn = require('../assets/translations/en.json');
const PolicyEs = require('../assets/translations/es.json');
let policy = null;

class PrivacyText extends Component {

    constructor(props) {
        super(props);
      }

    render(){

        let {t, language} = this.props;

        if (language === 'en') {
            policy = PolicyEn
          } else {
            policy = PolicyEs
          }

        return(
            <div className="text-justify policy">

            {             
                policy.privacy_policy.parrafAll.map((item, i) => {
                return <div className="text-justify" key={i}> 
                            
                            <h3>{item.title} </h3>
                            <p className="nav-item fadeInUp">{item.parraf}</p>
                            <p className="nav-item fadeInUp">{item.parraf2}</p>
                            <p className="nav-item fadeInUp">{item.parraf3}</p> 
                            <p className="nav-item fadeInUp">{item.parraf4}</p>                                              
                      </div>
                })
              
            }


                {/* <p>{t(this.props.parraf1)}</p>
                <p>{t(this.props.parraf2)}</p>
                <h3>{t(this.props.title1)}</h3>
                <p>{t(this.props.parraf3)}</p> */}

            </div>

         )
    }
}

export default translate('privacy_policy')(PrivacyText);