import React, {Component} from 'react';


class CasosExitoItems extends Component{
   render(){
        let {image, name, description } = this.props;
    return(
        <div className="cases_exi_item">
            <img src={require('../../img/home/' + image).default} alt=""/>
    
            <div className="hover_content_casos">
                <div className="content">
                    <h5>{name}</h5>
                    <p>{description}</p>
                </div>
            </div>

        </div>

        )
   }
}
export default CasosExitoItems;