import React, {Component} from 'react';
import Reveal from 'react-reveal/Reveal/';
import { translate } from "react-translate";


class SecBanner extends Component{
    render(){


            let { t , idBanner} = this.props;

        return(
            <section className="banner_area sec_pad" id={idBanner}>
                <div className="container">
                    <div className="banner_top">                   
                        <div className="row">
                            <div className="col-md-12 text-center seo_banner_content">
                                <Reveal effect="fadeInUp" duration={500}>
                                    <h2>{t(this.props.title_banner)}</h2>
                                
                                </Reveal>
                                <Reveal effect="fadeInUp" duration={1000}>
                                    <p className="fadeInUp" data-wow-delay="0.5s">
                                        {t(this.props.descri_banner)}</p>
                                </Reveal>
                                
                            </div>
                        </div>
                        <section id="section10" class="demo mt-5">
                                    <div><span></span></div>
                                </section>
                                <p>Scroll</p>
                    </div>
                </div>
            </section>
        )
    }    
}


export default translate('sec_banner')(SecBanner);
