import React, {Component} from 'react';
import Plx from "react-plx";
import { translate } from "react-translate";
import SeoTitle from './Title/SeoTitle';
import { HashLink } from 'react-router-hash-link';



class OurProcess extends Component {
    render(){
        let { t } = this.props;
        const ParallaxProcess = [
            {
              start: 'self',
              duration: 400,
              properties: [
                
                  {
                    startValue: 15,
                    endValue: 15,
                    property: "padding-right"
                  },
                  {
                    startValue: 15,
                    endValue: 15,
                    property: "padding-left"
                  }
              ]
            }
        ];

        const ParallaxProcessTwo = [
            {
              start: 'self',
              duration: 400,
              properties: [
                
                {
                    startValue: 15,
                    endValue: 15,
                    property: "padding-right"
                  },
                  {
                    startValue: 15,
                    endValue: 15,
                    property: "padding-left"
                  }
              ]
            }
        ];
        
        return(
        
            <section className="our_process_area bg-light" id="process">

                <SeoTitle Title={t(this.props.process_home_title)} TitleP={t(this.props.process_home_subtitle)}/>           
                <div className="container-fluid">
                
                    <div className="col-lg-12 pb-3 col_scrum">
                    
                        <div className="our_process_feedback">
                            
                            
                                <div className="clients_feedback_item row">
                                    <div className="col-lg-6 align-self-center ">
                                        <div className="feedback_body">
                                        <h3 className="f_name">Agile Scrum</h3>
                                            <p>{t(this.props.process_scrum_d)}</p>
                                            <a href="/scrum" className="learn_btn"><strong>{t(this.props.read_more)}</strong><i className="ti-arrow-right"></i></a>
                                            {/* <HashLink className="learn_btn" activeClass="active" to="/scrum#bannerScrum" spy={true} smooth={true} offset={0} duration={500}><strong>{t(this.props.read_more)}</strong><i className="ti-arrow-right"></i></HashLink> */}

                                        </div>
                                    </div>
                                    <div className="col-lg-6 align-self-center ">
                                        <Plx className="agency_featured_img text-right" parallaxData={ParallaxProcessTwo}>
                                            <img className="paddingg" src={require("../img/process/img-scrum.png").default} alt=""/>
                                        </Plx>
                                    </div>
                                    
                                </div>
                            
                        </div>
                    </div>

                    <div className="col-lg-12 pt-2 col_devops " >
                        <div className="our_process_feedback process_two">                           
                            
                                <div className="clients_feedback_item row flex-row-reverse">
                                    <div className="col-lg-6 align-self-center ">
                                        <div className="feedback_body">
                                        <h3 className="f_name">DevOps</h3>
                                            <p>{t(this.props.process_devops_d)}</p>
                                            <a href="/devops" className="learn_btn"><strong>{t(this.props.read_more)}</strong><i className="ti-arrow-right"></i></a>
                                            {/* <HashLink className="learn_btn" activeClass="active" to="/devops#bannerDevops" spy={true} smooth={true} offset={0} duration={500}><strong>{t(this.props.read_more)}</strong><i className="ti-arrow-right"></i></HashLink> */}
                                        </div>
                                    </div>
                                    <div className="col-lg-6 align-self-center ">                                  
                                        <img className="paddingg" src={require("../img/process/img-devops.png").default} alt=""/>
                                    </div>                                      
                                </div>
                            
                        </div>
                    </div>
                    
                </div>
            </section>
        
        )
    }
}

export default translate('process_home')(OurProcess);

