import React, {Component} from "react";
import Slider from 'react-slick';
import SeoTitle from '../Title/SeoTitle';
import { translate } from "react-translate";
import CasosExitoItems from '../Casos/CasosExitoItems';

const sucesscasesEn = require('../../assets/translations/en.json');
const sucesscasesEs = require('../../assets/translations/es.json');
let sucesscases = null;


class CasosExito extends Component {
    constructor(props){
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
/*        this.state={
            items:[
                {
                    "id": 1,
                    "image": "grid1.jpg",
                    "description": "Loo car boot bodge smashing I up the duff horse play give us a bell, William bugger all mate happy days mush at public school tosser victoria sponge, say lurgy hunky-dory twit hotpot gutted mate.",
                    "name": "Success Cases 1",
                },
                {
                    "id": 2,
                    "image": "grid1.jpg",
                    "description": "Loo car boot bodge smashing I up the duff horse play give us a bell, William bugger all mate happy days mush at public school tosser victoria sponge, say lurgy hunky-dory twit hotpot gutted mate.",
                    "name": "Success Cases 1",
                },
                {
                    "id": 3,
                    "image": "grid1.jpg",
                    "description": "Loo car boot bodge smashing I up the duff horse play give us a bell, William bugger all mate happy days mush at public school tosser victoria sponge, say lurgy hunky-dory twit hotpot gutted mate.",
                    "name": "Success Cases 1",
                }
            ]
        }*/

    }
    next() {
    this.slider.slickNext();
    }
    previous() {
    this.slider.slickPrev();
    }
    render(){

        let { t , language } = this.props;

        if (language === 'en') {
            sucesscases = sucesscasesEn
          } else {
            sucesscases = sucesscasesEs
          }

        const settings = {
            dots: false,
            infinite: true,
            speed: 500,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 2000,
            slidesToShow: 2,
            slidesToScroll: 1,
            responsive: [
                {
                  breakpoint: 700,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                  }
                },
                {
                  breakpoint: 480,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                  }
                }
              ]
        };
        return(
            <section className="cases_exi_area sec_pad" id='cases'>
                <div className="container">
                    <div className="cases_exi_content">
                        <SeoTitle 
                        Title={t(this.props.casos_title)} 
                        TitleP={t(this.props.casos_title_d)}
                        Tclass="text-center"
                        />
                        <div className="row">
                            <div className="slider_nav">
                                <i className="arrow_left prev" onClick={this.previous}></i>
                                <i className="arrow_right next" onClick={this.next}></i>
                            </div>
                            <Slider ref={c => (this.slider = c)} className="cases_exi_info mt_70" {...settings}>
                                {                          
                                    sucesscases.sucess_cases.casos_items.map((item, i) =>{ 
                                        return (
                                        <div className="item" key={item.id}>    
                                            <CasosExitoItems    
                                                image = {item.image}             
                                                name = {item.name}
                                                description = {item.description}
                                            /> 
                                        </div>
                                        )
                                    }) 
                                }
                            </Slider>
                        </div>
                </div>
                </div>
            </section>
        )
    }
}


export default translate('sucess_cases')(CasosExito);