import React, {Component} from 'react';
import Slider from 'react-slick';
import SeoTitle from '../Title/SeoTitle';
import AppTestimonialItems from '../Home/AppTestimonialItems';
import { translate } from "react-translate";

const TestimonialEn = require('../../assets/translations/en.json');
const TestimonialEs = require('../../assets/translations/es.json');
let testimonial = null;

class AppTestimonial extends Component{
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
    next() {
    this.slider.slickNext();
    }
    previous() {
    this.slider.slickPrev();
    }
   render(){
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 90000,
        slidesToShow: 1,
        slidesToScroll: 1,
    };

    let { t , language} = this.props;

    if (language === 'en') {
        testimonial = TestimonialEn
      } else {
        testimonial = TestimonialEs
      }

    
    return(
            <section className="app_testimonial_area bg-light sec_area" id="testimonial">
                <div className="container nav_container">
                    <div className="app_testimonial_content bg-white">
                    <SeoTitle 
                        Title={t(this.props.testim_title)}
                        Tclass="text-center"
                    />
                        <div className="slider_nav mt-5">
                            <i className="ti-arrow-left prev" onClick={this.previous}></i>
                            <i className="ti-arrow-right next" onClick={this.next}></i>
                        </div>
                        <Slider ref={c => (this.slider = c)} className="app_testimonial_slider" {...settings}>
                        {             
                            testimonial.testimonios.testimoniosItems.map((item, i) => {
                            return (
                                <div key={i}> 
                                    <AppTestimonialItems 
                                        NameT = {item.NameT}
                                        Position = {item.Position} 
                                        DescripT ={ item.DescripT} 
                                    />                                            
                                </div>
                                )
                            })
              
                        }
                        </Slider>
                    </div>
                </div>
            </section>
        )
   }
}

export default translate('testimonios')(AppTestimonial);

