import React from 'react';
import ServicesBanner from '../components/Banner/ServicesBanner';
import MetaDecorator from '../components/MetaDecorator';


const ServiceSoftware = () => {
    return(
        <div className="body_wrapper">
                <MetaDecorator 
                    title="Software Development Service"
                    description="DreamCode is a professional software development company dedicated to modernizing business objectives with leading-edge technology."
                />
                <ServicesBanner 
                    ClassC='service_1' 
                    Title= "TitleSoftwareDev"
                    TitleN= "TitleNSoftwareDev"
                    TitleP= "TitlePSoftwareDev"
                    Simage='slider-software.png'/>
        </div>
    )
}
export default ServiceSoftware;